.product
{
    display: flex;
    flex-direction: column;
    border: 1px solid gray;
    padding: 20px;
    border-radius: 15px;
}

.product__title
{
    margin-top: 0;
    font-size: 21px;
    margin-bottom: 15px;
}

.product__price
{
    font-size: 18px;
    margin-bottom: 15px;
}

.product__preview
{
    display: block;
    width: 100%;
    height: 200px;
    object-fit: contain;
    margin-bottom: 20px;
}

.product__add-basket-btn
{
    margin-top: auto;
}